.con1{
    width: 100%;
    background: linear-gradient(to right, #c5c5c5 0%, rgb(255, 248, 248) 100%);
    box-shadow: 0px 0px 5px 5px rgb(78, 78, 78);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 20px 0px;
}
.card1{
    width: 19.5%;
    min-width: 220px;
    height: 370px;
    background-color: rgb(255, 255, 255);
    margin-bottom: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.card1:hover{
    box-shadow: 0px 0px 5px 5px rgb(78, 78, 78);
}
.card-top1{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px 20px 0px;
    height: 350px;
}
.card1 .img1{
    width: 100%;
    height: 140px;
    display: flex;
    justify-content: center;
}
.card1 .img1 img{
    width: 100%;
    height: 100%;
}
.title1{
    font-size: 15px;
    height: 20px;
    overflow: hidden;
}
.rating1{
    display: flex;
    gap: 5px;
    font-size: 15px;
}
.rate1{
    padding: 1px 4px 3px 5px;
    display: flex;
    background-color: rgb(11, 119, 11);
    color: yellow;
    font-size: 13px;
    border-radius: 3px;
}
.star1 i{
    font-size: 12px;
    padding-left: 2px;
}
.price1{
    font-weight: bolder;
}
.desc1{
    font-size: 12px;
    height: 33px;
    overflow: hidden;
}
.count11{
    color: rgb(10, 68, 50);
}
.qty1{
    padding: 0px 5px;
}
.count11 button{
    background-color: rgb(207, 207, 207);
    border-radius: 50%;
}
.total1{
    color: rgb(10, 68, 50);
    font-weight: 600;
}
.total-amount-value1{
    font-size: 18px;
    font-weight: 700;
}

.card-bot1{
    display: flex;
    justify-content: center;
    height: 37px;
    align-items: center;
}
.card-bot1 button{
    width: 98%;
    height: 30px;
    background-color: rgb(60, 90, 90);
    color: #ffff00;
    box-shadow: 0px 0px 3px 3px #463c3c;
    font-size: 17px;
}
.card-bot1 button:hover{
    box-shadow: 0px 0px 5px 5px rgb(52, 123, 124);
    background-color: rgb(84, 154, 154);
    color: azure;
}
.grand-total1{
    width: 100%;
    font-weight: bolder;
    text-align: center;
}
.payment1{
    width: 100%;
    text-align: center;
    margin-top: 5px;
}
.payment1 button{
    padding: 5px 20px;
    border-radius: 10px;
    font-size: 15px;
    border: 1px solid #ffff00;
    background-color: #ffff00;
}

@media screen and (max-width: 500px) {
    .card1{
        min-width: 190px;
        height: 320px;
    }
}