.nav{
    width: 100%;
    height: 7vh;
    background: linear-gradient(to right, #434343 0% 100%);
    box-shadow: 0px 0px 5px 5px rgb(239, 0, 0);
    padding: 0px 50px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 25px;
}
.nav a{
    text-decoration: none;
    color: aquamarine;
}
.nav a:hover{
    font-size: 28px;
}

.cart-item{
    color: rgb(111, 0, 255);
}