.con{
    width: 100%;
    background: linear-gradient(to right, #c5c5c5 0%, rgb(255, 248, 248) 100%);
    box-shadow: 0px 0px 5px 5px rgb(78, 78, 78);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 20px 0px;
}
.card{
    width: 19.5%;
    min-width: 220px;
    height: 370px;
    background-color: rgb(255, 255, 255);
    margin-bottom: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.card:hover{
    box-shadow: 0px 0px 5px 5px rgb(78, 78, 78);
}
.card-top{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px 20px 0px;
    height: 320px;
}
.card .img{
    width: 100%;
    height: 180px;
    display: flex;
    justify-content: center;
}
.card .img img{
    width: 100%;
    height: 100%;
}
.title{
    font-size: 15px;
    height: 20px;
    overflow: hidden;
}
.rating{
    display: flex;
    gap: 5px;
    font-size: 15px;
}
.rate{
    padding: 1px 4px 3px 5px;
    display: flex;
    background-color: rgb(11, 119, 11);
    color: yellow;
    font-size: 13px;
    border-radius: 3px;
}
.star i{
    font-size: 12px;
    padding-left: 2px;
}
.price{
    font-weight: bolder;
}
.desc{
    font-size: 12px;
    height: 50px;
    overflow: hidden;
}
.card-bot{
    display: flex;
    justify-content: center;
    height: 37px;
    align-items: center;
}
.card-bot button{
    width: 98%;
    height: 30px;
    background-color: rgb(60, 90, 90);
    color: #ffff00;
    box-shadow: 0px 0px 3px 3px #463c3c;
    font-size: 17px;
}
.card-bot button:hover{
    box-shadow: 0px 0px 5px 5px rgb(52, 123, 124);
    background-color: rgb(84, 154, 154);
    color: azure;
}


@media screen and (max-width: 500px) {
    .card{
        min-width: 190px;
        height: 320px;
    }
}